import React  from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import Container from '../../../../components/Container';
import floor from './floor-4.svg';

import getSliderFloor from '../../getSliderFloor';

import projects from '../../projects/4';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  image {
    cursor: pointer;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const imageContainerWidth = 755;

const SliderFloor = getSliderFloor(4, projects, imageContainerWidth);

class Floor4 extends SliderFloor {
  updateSvgContent = (index) => {
    super.updateSvgContent(index);
  }

  // svg載入之後，從html參照中尋找目標
  handleSvgLoad = () => {
    // super，讀取 SliderFloor 中寫好的 handleSvgLoad
    super.handleSvgLoad();
  }

  render() {
    return (
      <Container innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Container>
    );
  }
}

export default Floor4;



