import React from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';
// import { themeGet } from 'styled-system';
import TWEEN from '@tweenjs/tween.js';

import getSliderFloor from '../../getSliderFloor';
import floor from './floor-3.svg';

import projects from '../../projects/3';

import Box from '../../../../components/Box';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const SliderFloor = getSliderFloor(3, projects, 396);

class Floor3 extends SliderFloor {
  render() {
    return (
      <Box innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Box>
    );
  }
}

export default Floor3;



