import getFloor from './getFloor';

export default (floor) => {
  const Floor = getFloor(floor);
  class AutoLinkFloor extends Floor {
    handleSvgLoad() {
      // super，讀取 Floor 中寫好的 handleSvgLoad
      super.handleSvgLoad();
      const floorButtons = this.container.querySelectorAll('.floor-btn');
      if (floorButtons) {
        floorButtons.forEach((btn) => {
          btn.addEventListener('click', () => {
            window.open(`${btn.dataset.link}`, '_blank');
          });
        });
      }

    }
  }

  return AutoLinkFloor;
};



