import React from 'react';
import PropTypes from 'prop-types';

import Box from '../Box';
import Circle from '../Circle';
import Button from '../Button';
import Svg from '../Svg';

const NoPaddingButton = (props) => (
  <Button
    px="0"
    py="0"
    color="lightBlue"
    bg="white"
    border="1px solid"
    borderColor="lightBlue"
    hoverBg="yellow"
    borderRadius="50%"
    {...props}
  />
);

const ArraowDown = ({ children, onClick, arrowType, ...props }) => (
  <Box {...props}>
    <Circle
      lineHeight="0"
      onClick={onClick}
      is={NoPaddingButton}
    >
      <Svg viewBox="0 0 60 60">
        <path fill="transparent" stroke="currentColor" d="M24.29 28.15L30 35.12l5.71-6.97"/>
      </Svg>
    </Circle>
  </Box>
);

ArraowDown.propTypes = {
  children: PropTypes.node,
  onClick: PropTypes.func,
};

export default ArraowDown;



