import React from 'react';
import styled, { css } from 'styled-components';
import {
  themeGet,
  fontSize,
  space,
  color,
  width,
  borders,
  borderColor,
  borderRadius,
  letterSpacing,
  fontWeight,
  position,
} from 'styled-system';
import tag from 'clean-tag';

import { getColorByPropKey } from './utils/getColor';
import blacklist from './utils/blacklist';

const active = css`
  color: ${getColorByPropKey('hoverColor')};
  background-color: ${getColorByPropKey('hoverBg')};
  border-color: ${getColorByPropKey('hoverBorder')};
`;

export const buttonStyle = css`
  padding: 0;
  border: none;
  font-family: inherit;
  line-height: 1;
  text-decoration: none;
  ${fontSize}
  ${position}
  ${space}
  ${color}
  ${width}
  ${borders}
  ${borderColor}
  ${borderRadius}
  ${fontWeight}
  ${letterSpacing}
  appearance: none;
  transition: all ${themeGet('duration', 250)}ms;
  cursor: pointer;
  &:hover,
  &:focus {
    ${(props) => !props.disabled && active}
  }
  ${(props) => props.active && !props.disabled && active}
  ${(props) => props.disabled && `
    cursor: not-allowed;
    opacity: 0.5;
  `}
`;

const Button = styled(tag)`
  ${buttonStyle}
`;

Button.defaultProps = {
  blacklist,
  is: 'button',
  f: '1em',
  bg: 'blue',
  color: 'white',
  hoverColor: 'blue',
  hoverBg: 'white',
  px: '0.5em',
  py: '0.25em',
  fontWeight: 'bold',
};

Button.secondary = (props) => (
  <Button
    bg="secondary"
    borderColor="secondary"
    hoverColor="secondary"
    {...props}
  />
);

Button.transparent = (props) => (
  <Button
    border="1px solid transparent"
    bg="transparent"
    color="text"
    hoverBorder="blue"
    {...props}
  />
);

export default Button;



