import React from 'react';
import { Element } from 'react-scroll'

import Box from '../components/Box';

import DeskElevatorButton from '../containers/Elevator/ElevatorButton';
import MobileElevatorButton from '../containers/Elevator/MobileElevatorButton';
import Intro from '../containers/Intro';
import Partners from '../containers/Partners';
import DesktopFloors from '../containers/Floors/Desktop';
import MobileFloors from '../containers/Floors/Mobile';

import withResponsive from '../hoc/withResponsive';

class Page extends React.PureComponent {
  state = {
    elevatorVisible: false,
    currentFloor: '5',
  }

  handleSetActiveFloor = (nextFloor) => {
    const { currentFloor } = this.state;
    if (nextFloor !== currentFloor) {
      const newState = { currentFloor: nextFloor };
      if (currentFloor === '5' && nextFloor === '4') newState.elevatorVisible = true;
      if (currentFloor === 'B2' && nextFloor === 'B1') newState.elevatorVisible = true;
      if (currentFloor === '4' && nextFloor === '5') newState.elevatorVisible = false;
      if (currentFloor === 'B1' && nextFloor === 'B2') newState.elevatorVisible = false;
      this.setState(newState);
    }
  }

  render() {
    const { browser } = this.props;
    const { elevatorVisible, currentFloor } = this.state;
    const Floors = browser.greaterThan.xs ? DesktopFloors : MobileFloors;
    const ElevatorButton = browser.greaterThan.xs ? DeskElevatorButton : MobileElevatorButton;
    return (
      <Box overflow="hidden">
        <Element name="5">
          <Intro />
        </Element>
        <Floors currentFloor={currentFloor} />
        <Element name="B2">
          <Partners />
        </Element>
        <ElevatorButton
          visible={elevatorVisible}
          currentFloor={currentFloor}
          onSetActiveFloor={this.handleSetActiveFloor}
        />
      </Box>
    );
  }
}

export default withResponsive(Page);



