import React from 'react';

import Container from '../../components/Container';
import Text from '../../components/Text';
import BackgroundImage from '../../components/BackgroundImage';

import withResponsive from '../../hoc/withResponsive';

import partner from './partners.png';
import partnerMobile from './partners-mobile.png';

const Partners = ({ browser, ...props }) => {
  const isMobile = browser.lessThan.sm;
  return (
    <Container textAlign="center" py="2em" {...props}>
      <Text.h2 f="2em" color="blue">商業及合作夥伴</Text.h2>
      <Text my="1em">我們曾經合作過的夥伴，感謝他們的幫助</Text>
      <BackgroundImage
        my="3em"
        src={isMobile ? partnerMobile:  partner}
        ratio={isMobile ? 542 / 751 : 300 / 1280}
      />
    </Container>
  );
}

export default withResponsive(Partners);



