import React from 'react';
import Box from '../../components/Box';
import Cloud from '../../components/Cloud';

const locations = [
  {
    w: ['30%', null, '15%'],
    top: '30%',
    right: ['5%', null, '10%'],
  },
  {
    w: ['50%', null, '25%'],
    top: '5%',
    left: '5%',
  },
  {
    w: [0, null, '13%'],
    top: '60%',
    left: '-5%',
  },
];

export default (props) => (
  <Box {...props}>
    {locations.map((location, index) => (
      <Cloud
        key={index}
        position="absolute"
        top={location.top}
        right={location.right}
        left={location.left}
        w={location.w}
      />
    ))}
  </Box>
);



