import round from 'lodash/round';

import FetchBase64 from '../../vendors/fetch-base64';
import getFloor from './getFloor';

export default (floor, projects, imageContainerWidth) => {
  const Floor = getFloor(floor);
  const projectCount = projects.length;
  class AutoSliderFloor extends Floor {
    constructor(props) {
      super(props);
      this.updateSvgContent = this.updateSvgContent.bind(this);
    }

    state = {
      current: 0,
    }

    componentDidMount() {
      // 預先讀取全部的照片
      projects.forEach(({ src }, index) => {
        const fetcher = new FetchBase64(src);
        fetcher.fetchAsData().then(this.handleImageFetch(index));
      });
    }

    getNewScale = (width) => {
      const [, oldScale] = /scale\(([^)]+)/g.exec(this.imageTransform);
      return this.imageTransform.replace(oldScale, round(imageContainerWidth / width, 2));
    }

    // 取好預設的 this.imageUris 為空陣列
    imageUris = []

    // 每次setState之後，即會觸發
    componentWillUpdate(nextProps, nextState) {
      if (this.updateSvgContent) this.updateSvgContent(nextState.current);
    }

    updateSvgContent(index) {
      if (this.title) {
        // 更新title的文字
        this.title.innerHTML = projects[index].title;
      }

      if (this.image) {
        // 更新image
        this.image.setAttribute('xlink:href', this.imageUris[index].dataurl);
        this.image.setAttribute('width', this.imageUris[index].width);
        this.image.setAttribute('height', this.imageUris[index].height);
        this.image.setAttribute('transform', this.getNewScale(this.imageUris[index].width));
      }
    }

    handleBtnClick = (index) => () => {
      const { current } = this.state;
      if (index === 0) {
        // 向後
        if (current === 0) {
          this.setState({ current: projectCount - 1 });
        } else {
          this.setState({ current: current - 1 });
        }
      } else {
        // 向前
        if (current === projectCount - 1) {
          this.setState({ current: 0 });
        } else {
          this.setState({ current: current + 1 });
        }
      }
    }

    // 把讀到的datauri存到 this.imageUris 裡
    handleImageFetch = (index) => (datauri) => {
      this.imageUris[index] = datauri;
    }

    handleImageOnClick = () => window.open(projects[this.state.current].link, '_blank')

    handleSvgLoad() {
      // super，讀取 Floor 中寫好的 handleSvgLoad
      super.handleSvgLoad();
      const floorBtns = this.container.querySelectorAll('.floor-btn');
      if (floorBtns) {
        floorBtns.forEach((btn, index) => {
          btn.addEventListener('click', this.handleBtnClick(index));
        });
      }
      // 在html中，尋找唯一的image tag
      this.image = this.container.querySelector('image');
      if (this.image) {
        this.imageTransform = this.image.getAttribute('transform');
        this.imageOnClick = this.image.addEventListener('click', this.handleImageOnClick);
      }
      this.title = this.container.querySelector('.floor-title');
    }
  }

  return AutoSliderFloor;
};




