import React from 'react';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Text from '../../components/Text';
import DoubleLayerCircle from '../../components/DoubleLayerCircle';
import Border from '../../components/Border';

export default ({ label, active, children, ...props }) => (
    <Flex align="center" justify="center" mt="0.25em" {...props}>
      <Box w="2.8em" f="0.8em">
        <DoubleLayerCircle
          className="elevator-button"
          bg={active ? 'yellow' : 'white'}
          fontWeight="bold"
          color="blue"
          w={1}
        >
          {label}
        </DoubleLayerCircle>
      </Box>
      {children && (
        <Border borderBottom="1px solid lightBlue" mx="0.5em">
          <Text fontWeight="bold" color={active && 'blue'}>{children}</Text>
        </Border>
      )}
    </Flex>
);



