import React from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-1.svg';

import getAutoLinkFloor from '../../getAutoLinkFloor';
import Container from '../../../../components/Container';
import Cloud from '../../../../components/Cloud';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      path:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
        stroke: white;
      }
      path:nth-of-type(1) {
        fill: ${themeGet('colors.yellow')};
        stroke: white;
      }
    }
  }
  .floor-btn-disable {
    cursor: not-allowed;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      circle {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getAutoLinkFloor(1);

class Floor1 extends AutoFloor {
  render() {
    return (
      <Container innerRef={this.handleContainerRef}>
        <Cloud
          position="absolute"
          w="33%"
          right="0"
          bottom="35%"
          transform="translateX(100%)"
        />
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Container>
    );
  }
}

export default Floor1;



