import React from 'react';

import Box from '../Box';
import BackgroundImage from '../BackgroundImage';
import cloud from './cloud.svg';

const Cloud = (props) => (
  <Box position="absolute" {...props}>
    <BackgroundImage ratio={175 / 452} src={cloud} />
  </Box>
);

export default Cloud;



