import React from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-2.svg';

import getAutoLinkFloor from '../../getAutoLinkFloor';
import Container from '../../../../components/Container';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      > path:nth-of-type(1) {
        fill: ${themeGet('colors.yellow')};
      }
    }
  }
  .floor-btn-disabled {
    cursor: not-allowed;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getAutoLinkFloor(2);

class Floor2 extends AutoFloor {
  render() {
    return (
      <Container innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Container>
    );
  }
}

export default Floor2;



