import { PureComponent } from 'react';
import { push } from 'gatsby-link'

export default (floor) => {
  class Floor extends PureComponent {
    constructor(props) {
      super(props);
      this.handleSvgLoad = this.handleSvgLoad.bind(this);
    }

    handleSvgLoad() {
      const moreBtn = this.container.querySelector('.floor-morebtn');
      if (moreBtn) {
        moreBtn.addEventListener('click', () => {
          push(`/portifolio/${floor}`);
        });
      }
    }

    handleContainerRef = (ref) => {
      this.container = ref;
    }
  }

  return Floor;
};



