import React from 'react';
import { Link as ScrollLink } from 'react-scroll';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import envelope from '../envelope.svg';
import relab from '../relab.svg';

import Box from '../../../components/Box';
import Link from '../../../components/Link';
import Flex from '../../../components/Flex';
import Text from '../../../components/Text';
import BackgroundImage from '../../../components/BackgroundImage';
import DoubleLayerBox from '../../../components/DoubleLayerBox';
import Border from '../../../components/Border';
import LinksButtonhref from '../LinksButtonhref';
import FloorButton from '../FloorButton';
import floors from '../floors.json';

const contacts = [
  {
    img: envelope,
    text: '案件合作',
    to: '/contact',
  },
  {
    img: relab,
    text: '關於我們',
    to: '/about',
  },
];

const StyledLink = styled(Link)`
  &:hover {
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

const StyledScrollLink = styled(ScrollLink)`
  cursor: pointer;
  &:hover {
    color: ${themeGet('colors.blue')};
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

export default ({ visible, onSetActiveFloor, currentFloor }) => (
  <Box
    position="fixed"
    right="2em"
    top="50vh"
    transform="translateY(-50%)"
    opacity={Number(visible)}
    pointerEvents={visible ? 'auto' : 'none'}
    zIndex={99}
    transition="opacity 500ms"
  >
    <DoubleLayerBox>
      <Box px="1em" py="1em">
        <Box bg="blue" p="0.5em" w="5em" mx="auto" mb="1em">
          <Border borderBottom="1px solid white">
            <Text align="center" f="2em" color="white">{currentFloor}</Text>
          </Border>
        </Box>
        <Box>
          {floors.map((f) => (
            <StyledScrollLink
              key={f.floor}
              to={f.floor}
              spy
              smooth
              duration={1000}
              offset={-window.innerHeight / 4}
              onSetActive={onSetActiveFloor}
            >
              <FloorButton label={f.floor} active={f.floor === currentFloor}>
                {f.text}
              </FloorButton>
            </StyledScrollLink>
          ))}
          {['5', 'B2'].map((key) => (
            <StyledScrollLink
              key={key}
              to={key}
              spy
              offset={-window.innerHeight / 4}
              onSetActive={onSetActiveFloor}
            />
          ))}
          <StyledLink to="/portifolio">
            <FloorButton label="All">
              所有作品
            </FloorButton>
          </StyledLink>
        </Box>
        <Flex align="center" justify="center">
          {contacts.map((contact, index) => (
            <Box f="0.8em" w="4.5em" mt="1em" key={index}>
              <Box mx="10%">
                <LinksButtonhref
                  to={contact.to}
                  bg="transparent"
                  hoverBg="yellow"
                  w={1}
                  px="0.25em"
                  py="0.125em"
                >
                  <BackgroundImage src={contact.img} ratio={13 / 17} />
                </LinksButtonhref>
              </Box>
              <Border borderBottom="1px solid lightBlue" mx="0.25em" mt="0.5em">
                <Text fontWeight="bold">{contact.text}</Text>
              </Border>
            </Box>
          ))}
        </Flex>
      </Box>
    </DoubleLayerBox>
  </Box>
);



