import React  from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import Box from '../../../../components/Box';
import floor from './floor-4.svg';

import getSliderFloor from '../../getSliderFloor';
import projects from '../../projects/4';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }

  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const SliderFloor = getSliderFloor(4, projects, 556);

class Floor4 extends SliderFloor {
  render() {
    return (
      <Box innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Box>
    );
  }
}

export default Floor4;



