import React from 'react';
import { Link } from 'react-scroll';
import { push } from 'gatsby-link';

import Container from '../../components/Container';
import Box from '../../components/Box';
import Text from '../../components/Text';
import Image from '../../components/Image';
import BackgroundImage from '../../components/BackgroundImage';
import ArrowDown from '../../components/ArrowDown';
import Clouds from './Clouds';
import Pigeon from './Pigeon';

import logo from './logo.svg';
import relabyo from './relabyo.svg';

const Intro = () => (
  <Box position="relative" overflow="hidden" pb="10vh">
    <Clouds />
    <Box position="absolute" top="0" right="0" transform="translate(-20%, 50%)" minWidth="6em" w="8%">
      <BackgroundImage w="100%" src={relabyo} ratio={42 / 130} />
    </Box>
    <Container pt={['15vh', null, '30vh']}>
      <Box position="relative" align="center">
        <Box w="50%" maxWidth="8em" mx="auto">
          <Image src={logo} />
        </Box>
        <Pigeon
          position="absolute"
          w="25%"
          left={['4em', null, 0]}
          bottom="0"
          minWidth="15em"
          transform={['translate(-50%,-15%)', null, 'translate(-50%,-10%)']}
          onClick={() => push('/contact')}
        />
        <Text.h6 fontWeight="200" mt="1.5rem" mx="2em">
          <Text f="1.25em">
          我們是一家資訊設計顧問公司，讓你的資訊透過設計，打動人心
          </Text>
        </Text.h6>
        {/* <Text.h6 fontWeight="200" my="1rem">
          運用資訊設計實驗，建構不同的溝通模式看到更多溝通的機會。
        </Text.h6> */}
        <Box mt={['16em', null, '13em']}>
          <Text.h6 fontWeight="200" my="1rem">
            Take a Peek
          </Text.h6>
          <Link
             to="4"
             spy
             smooth
             duration={1000}
          >
            <ArrowDown mt="-0.5em" w="3.5em" mx="auto" />
          </Link>
        </Box>
      </Box>
    </Container>
  </Box>
);

export default Intro;



