import React from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-1.svg';

import getSliderFloor from '../../getSliderFloor';
import Box from '../../../../components/Box';
import projects from '../../projects/1';

const StyledSvg = styled(SVG)`
  image {
    cursor: pointer;
  }
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      circle {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getSliderFloor(1, projects, 276);

class Floor1 extends AutoFloor {
  render() {
    return (
      <Box innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
        />
      </Box>
    );
  }
}

export default Floor1;



