import React from 'react';
import { Link as ScrollLink } from 'react-scroll';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import envelope from '../envelope.svg';
import relab from '../relab.svg';

import Box from '../../../components/Box';
import Link from '../../../components/Link';
import Flex from '../../../components/Flex';
import BackgroundImage from '../../../components/BackgroundImage';
import LinksButtonhref from '../LinksButtonhref';
import FloorButton from '../FloorButton';
import floors from '../floors.json';

const contacts = [
  {
    img: envelope,
    text: '案件合作',
    to: '/contact',
  },
  {
    img: relab,
    text: '關於我們',
    to: '/about',
  },
];

const StyledLink = styled(Link)`
  width: 3.25em;
  &:hover {
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

const StyledScrollLink = styled(ScrollLink)`
  cursor: pointer;
  width: 3.25em;
  &:hover {
    color: ${themeGet('colors.blue')};
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

export default ({ visible, onSetActiveFloor, currentFloor }) => (
  <Box position="relative">
    <Box
      position="fixed"
      top="0"
      opacity={Number(visible)}
      pointerEvents={visible ? 'auto' : 'none'}
      zIndex={99}
      transition="opacity 500ms"
      my="auto"
      bg="#f7f7f7"
      w="100%"
    >
      <Flex justify="center" align="center" my="0.5em">
        {floors.map((f) => (
          <StyledScrollLink
            key={f.floor}
            to={f.floor}
            spy
            smooth
            duration={1000}
            offset={-window.innerHeight / 4}
            onSetActive={onSetActiveFloor}
          >
            <FloorButton f="1.3em" mx="0.25rem" label={f.floor} active={f.floor === currentFloor} />
          </StyledScrollLink>
        ))}
        <StyledLink to="/portifolio">
          <FloorButton mx="0.25rem" f="1.3em" label="All" />
        </StyledLink>
        {contacts.map((contact, index) => (
          <LinksButtonhref
            key={index}
            to={contact.to}
            bg="transparent"
            hoverBg="yellow"
            w="3.25em"
            mx="0.25rem"
            px="0.25em"
            py="0.125em"
          >
            <BackgroundImage src={contact.img} ratio={13 / 17} />
          </LinksButtonhref>
        ))}
      </Flex>
      {['5', 'B2'].map((key) => (
        <StyledScrollLink
          key={key}
          to={key}
          spy
          offset={-window.innerHeight / 4}
          onSetActive={onSetActiveFloor}
        />
      ))}
    </Box>
    {/* <Box
      position="fixed"
      top="50vh"
      right="1em"
      transform="translateY(-50%)"
      opacity={Number(visible)}
      pointerEvents={visible ? 'auto' : 'none'}
      zIndex={99}
      transition="opacity 500ms"
      my="auto"
    >
      <DoubleLayerBox>
        <Box px="1em" py="1em">
          <Box bg="blue" px="0.5em" py="0.25em" w="4em" mx="auto" mb="0.5em">
            <Border borderBottom="1px solid white">
              <Text align="center" f="2em" color="white">{currentFloor}</Text>
            </Border>
          </Box>
        </Box>
      </DoubleLayerBox>
    </Box> */}
  </Box>
);



