import React from 'react';
import { Element } from 'react-scroll'

import Box from '../../../components/Box';
import Cloud from '../../../components/Cloud';

import Floor4 from './Floor4';
import Floor3 from './Floor3';
import Floor2 from './Floor2';
import Floor1 from './Floor1';
import FloorB1 from './FloorB1';

const DesktopFloors = ({ currentFloor }) => (
  <Box position="relative">
    <Box position="relative">
      <Cloud
        w="15%"
        top="-10%"
        right="-2%"
      />
      <Element name="4">
        <Floor4 />
      </Element>
    </Box>
    <Element name="3">
      <Floor3 active={currentFloor === '3'} />
    </Element>
    <Element name="2">
      <Floor2 />
    </Element>
    <Element name="1">
      <Floor1 />
    </Element>
    <Element name="B1">
      <FloorB1 />
    </Element>
  </Box>
);

export default DesktopFloors;



