import React, { createElement } from 'react';
import PropTypes from 'prop-types';
import pickBy from 'lodash/pickBy';

import Border from './Border';
import Circle from './Circle';

const DoubleLayerCircleButton = ({
  m,
  mx,
  my,
  mt,
  ml,
  mb,
  mr,
  outerBg,
  border,
  borderRadius,
  borderColor,
  w,
  width,
  children,
  ...props
}) => {
  const margins = {
    m,
    mx,
    my,
    mt,
    ml,
    mb,
    mr,
  };
  return createElement(Border, {
    ...pickBy(margins, Boolean),
    display: 'inline-block',
    border: '1.5px solid',
    borderColor: props.disabled ? 'gray' : borderColor,
    borderRadius: '50%',
    p: '0.2em',
    bg: outerBg,
    w,
    width,
    lineHeight: '0',
  }, (
    <Border
      w={1}
      border={border}
      borderRadius={borderRadius}
      borderColor={borderColor}
      px="0"
      py="0"
      {...props}
    >
      <Circle>
        {children}
      </Circle>
    </Border>
  ));
};

const responsivePropTypes = PropTypes.oneOfType([
  PropTypes.number,
  PropTypes.string,
  PropTypes.array,
]);

DoubleLayerCircleButton.propTypes = {
  m: responsivePropTypes,
  mx: responsivePropTypes,
  my: responsivePropTypes,
  mt: responsivePropTypes,
  ml: responsivePropTypes,
  mb: responsivePropTypes,
  mr: responsivePropTypes,
  w: responsivePropTypes,
  width: responsivePropTypes,
  outerBg: PropTypes.string,
  border: PropTypes.string,
  borderColor: PropTypes.string,
  disabled: PropTypes.bool,
};

DoubleLayerCircleButton.defaultProps = {
  border: '1.5px solid',
  borderColor: 'lightBlue',
  borderRadius: '50%',
  outerBg: 'white',
};

export default DoubleLayerCircleButton;



