import React from 'react';
import SVG from 'react-inlinesvg';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-b1.svg';
import bg from './under-bg.svg';

import getAutoLinkFloor from '../../getAutoLinkFloor';

import Box from '../../../../components/Box';
import Container from '../../../../components/Container';

const UnderBg = styled(Box)`
  background-size: 33% auto;
  background-image: url(${bg});
  background-repeat: repeat;
`;

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    g {
      transition: opacity 0.25s;
    }
    &:hover {
      > g:nth-of-type(2) {
        opacity: 0;
      }
    }
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getAutoLinkFloor('B1');

class FloorB1 extends AutoFloor {
  render() {
    return (
      <UnderBg bg="lightGray">
        <Container innerRef={this.handleContainerRef}>
          <StyledSvg
            src={floor}
            onLoad={this.handleSvgLoad}
          />
        </Container>
      </UnderBg>
    );
  }
}

export default FloorB1;



